@import 'variables.scss';
button {
  background-color: white;
  color: $primary-red;
  border: none;
  border-radius: 7.5px;
  font-size: 16px;
  font-weight: bold;
  margin-top: 10px;
  padding: 10px 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

input, textarea {
  background-color: white;
  border: none;
  border-radius: 7.5px;
  color: black;
  font-size: 14px;
  margin: 10px 0;
  padding: 20px;
  width: 400px;
  height: 20px;
}

.disabled {
    opacity: 0.5;
}

.enabled {
    opacity: 1;
}

.radio-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 400px;
    .radio-container {
        display: flex;
        flex-direction: row;

        .radio-buttons {
            display: flex;
            flex-direction: row;
            p {
                font-size: 18px;
                font-weight: bold;
                display: flex;
                align-items: center;
            }
            .radio-button {
                display: flex;
                align-items: center;
                flex-wrap: wrap-reverse;
                input {
                    width: 25px;
                    margin-left: 15px;
                }
                label {
                    font-size: 18px;
                }
            }
        }
    }
}

.radio-container-test {
    display: flex;
    flex-direction: row;
}

.food-pref {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    p {
        font-size: 18px;
        font-weight: 300;
    }
    textarea {
        width: 400px;
        height: 72px;
    }
}

@media (max-width: 878px) {
    form {
        display: flex;
        flex-direction: column;
        align-items: center;
        input, textarea {
            margin-top: 5px;
            max-width: 80vw;
        }

        button {
            margin-top: 5px;
            align-self: flex-start;
            margin-left: 10vw;
        }
    }

    .radio-wrapper {
        width: 80vw;
        .radio-container {
            .radio-buttons {
                margin-bottom: 12px;
                p {
                  font-size: 12px;
                  font-weight: 500;
                }
                .radio-button {
                    input {
                        margin: 0 0 0 12px;
                    }
                    label {
                        font-size: 12px;
                    }
                }
            }
        }
    }
    .food-pref {
        display: flex;
        flex-direction: column;
        margin-top: 0px;
        p {
            font-size: 12px;
            font-weight: 500;
            text-align: center;
        }
        input {
            height: 40px;
            font-size: 12px;
        }
    }
}
